* {
  margin: auto;
}

h1 span {
  font-family: Montserrat;
  font-size: 32px;
  font-style: italic;
  font-weight: 700;
  line-height: 48px;
  text-align: left;
}

h1 {
  font-family: Montserrat;
  font-size: 32px;
  font-style: italic;
  font-weight: 500;
  line-height: 48px;
  text-align: left;
  color: white;
}

.leftPartContainer {
  max-width: 751px;
}

.mensIcon {
  max-width: 854px;
}

.containerHeaderContent {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btnHTSHeader {
  width: 493px;
  height: 96px;
  background: linear-gradient(
    225deg,
    #3d4eb0 0%,
    #4758b6 20.5%,
    #4959b7 24%,
    #3a4aa9 52.52%,
    #4b5bb5 74.5%,
    #4556b6 100%
  );

  box-shadow: 0px 4px 4px 0px #00000040;
  color: white;

  font-family: Montserrat;
  font-size: 24px;
  font-weight: 700;
  line-height: 24.38px;
  text-align: center;
  text-transform: none;
  margin-block-start: 60px;
}

.containerHeader {
  background-image: url(../../../images/bgheader.png);
  background: linear-gradient(
    44.71deg,
    rgb(0, 0, 0) 0%,
    rgb(29, 57, 82) 48.851%,
    rgb(12, 41, 64) 100.724%
  );
  padding-block-start: 170px;
}

.containerRecomended {
  background: linear-gradient(
    225deg,
    #000000 0%,
    #193a57 0%,
    #1d3952 51.19%,
    #0c2940 99.73%
  );

  text-align: center;
  color: white;
}

h3 {
  font-family: Montserrat;
  font-size: 36px;
  font-weight: 700;
  line-height: 43.88px;
  padding-block-start: 108px;
}

.containerStars {
  max-width: 1519px;
  display: flex;
  flex-wrap: wrap;
  margin-block-start: 50px;
  padding-block-end: 108px;
}

.blockStar {
  max-width: 493px;
  width: 100%;
  max-height: 219px;
  height: 219px;
  background: #000000;
  gap: 0px;
  margin-block-start: 12.5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.blockStar p {
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 500;
  line-height: 29.26px;
  text-align: center;
  max-width: 250px;
  margin-block-start: 0;
}

.blockStar img {
  width: 60px;
  height: 60px;
  margin-block-start: 50px;
}

.lastStar {
  margin-inline-end: 10px;
}

.lestStar {
  margin-inline-start: 10px;
}
