@media (max-width: 1024px) {
  .containerOpen {
    position: absolute;
    height: 100vh;
    width: 300px;
    top: 0;
    left: 0;
    display: flex;
    transition: 2s;
    background: #171717eb;
    box-shadow: 7px 0px 7.6px 0px #00000040;

    animation-name: OpenMenu;
    animation-duration: 0.5s;

    flex-direction: column;
    justify-content: start;
  }

  .containerOpen:first-of-type,
  .containerClose:first-of-type {
    padding-block-start: 50px;
  }

  .containerClose {
    position: absolute;
    height: 100vh;
    width: 300px;
    top: 0;
    left: -550px;
    background: #171717eb;
    box-shadow: 7px 0px 7.6px 0px #00000040;
    transition: 2s;
    animation-name: CloseMenu;
    animation-duration: 2s;
    animation-fill-mode: forwards;

    flex-direction: column;
    justify-content: start;
  }

  .containerOpen li,
  .containerClose li {
    margin-block: 29.5px;
    margin-inline: max-content;
    max-width: max-content;
    text-align: center;
    width: max-content;
  }

  .containerOpen li a,
  .containerClose li a {
    margin-inline: max-content;
    max-width: max-content;
  }

  .activeButton {
    position: absolute;
    top: 10px;
    left: -80px;
    transform: rotate(90deg);
    transition: 1s;
    background-color: transparent;
    z-index: 10;
  }

  .activeButton:focus,
  .activeButton:focus {
    outline: none;
    box-shadow: none;
    border: none;
  }

  nav button:first-child {
    display: block;
    background-color: transparent;

    position: absolute;
    top: 10px;
    left: -80px;
    transition: 1s;
  }

  button:focus,
  button:active {
    outline: none;
    box-shadow: none;
    border: none;
  }

  nav button:last-child {
    margin-block: 29.5px;
    margin-inline: 21.5px;
  }
}

@media (min-width: 1024px) {
  nav button:first-child {
    display: none;
  }
}

@keyframes OpenMenu {
  from {
    width: 0px;
    color: transparent;
    left: -550px;
  }
  99% {
    color: white;
  }
  to {
    width: 300px;
    left: 0px;
  }
}

@keyframes CloseMenu {
  from {
    width: 300px;
    left: 0;
  }
  99% {
    width: 120px;
  }
  to {
    width: 0px;
    left: -550px;
    color: transparent;
  }
}
