* {
  margin: auto;
}

.startPage {
  background: linear-gradient(
    225deg,
    #000000 0%,
    #193a57 0%,
    #1d3952 51.19%,
    #0c2940 99.73%
  );
  padding-block-start: 170px;
  padding-block-end: 100px;
}

.headerStart {
  background: linear-gradient(
    45deg,
    rgb(69, 91, 146) 0%,
    rgb(69, 101, 185) 47.818%,
    rgb(73, 97, 158) 100%
  );

  color: rgb(255, 255, 255);
  font-family: Montserrat;
  font-style: normal;
  font-size: 36px;
  font-weight: 900;
  line-height: 43.88px;
  letter-spacing: 0px;
  text-transform: uppercase;
  text-align: center;
  max-width: 1007px;
  height: 85px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-block-start: 100px;
}

.flexContainer {
  margin-block-start: 100px;
  max-width: 1600px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contextCard h4 {
  font-family: Montserrat;
  font-size: 36px;
  font-weight: 900;
  line-height: 43.88px;
  text-align: left;
  color: white;
}

.contextCard article {
  font-family: Montserrat;
  margin-block-start: 30px;

  font-size: 36px;
  font-weight: 600;
  line-height: 43.88px;
  max-width: 545px;
  color: white;

  text-transform: uppercase;
}

.contextCard p {
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 400;
  line-height: 29.26px;
  text-align: left;
  color: white;
  max-width: 539px;
}

.contextCard button {
  margin-block-start: 74px;
  width: 494px;
  height: 60px;
}

.contextCard p span {
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 800;
  line-height: 29.26px;
  text-align: left;
}

.containerCard__Start {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}

.containerCard__Start:not(:first-of-type) {
  margin-block-start: 100px;
}

.btnStartGame {
  width: 494px;
  height: 60px;
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 700;
  line-height: 21.94px;
  margin-block-start: 42px;
}
